<template>
  <div>
    <Loadings v-if="data == null"></Loadings>
    <div class="container" v-else>
      <div class="item">
        <div class="info">
          <div>名称: {{ data.data.name }}</div>
          <div>材质: {{ data.data.material }}</div>
          <div class="flex-sa">
            <div>数量: {{ data.data.number }}</div>
            <div>长度: {{ data.data.length }}</div>
            <div>高度: {{ data.data.width }}</div>
          </div>
          <div>备注: {{ data.data.remark }}</div>
        </div>
      </div>
      <div class="list" v-if="data.detail.length > 0">
        <div class="caption">工艺流程</div>
        <van-collapse v-model="activeNames">
          <van-collapse-item
            v-for="(item, indexs) in data.detail"
            :key="indexs"
            :title="item.pname"
            :name="indexs"
          >
          
            <van-cell-group v-if="role != 0">
              <van-field
                rows="2"
                autosize
                label="备注"
                type="textarea"
                maxlength="50"
                placeholder="请输入备注"
                v-model="item.remark"
                 :disabled="item.cstatus == 1 ? true : false" 
                show-word-limit
              />
              <div class="flex-sa">
                <van-field placeholder="材料总数" :disabled="item.cstatus == 1 ? true : false"  v-model="item.sum_num" />
                <van-field placeholder="完成数量"  :disabled="item.cstatus == 1 ? true : false"  v-model="item.com_num" />
              </div>
              <van-cell center title="是否完成" >
                <template #right-icon>
                  <van-switch  v-if="item.cstatus == 0" v-model="item.cstatus" size="24" />
                  <div v-else>已完成</div>
                </template>
              </van-cell>
              <van-button
                v-if="item.cstatus == 0"
                @click="subPro(indexs)"
                size="normal"
                block
                round
                type="info"
                >提交</van-button
              >
            </van-cell-group>
          </van-collapse-item>
        </van-collapse>
      </div>
    </div>
  </div>
</template>

<script>
import { setProduction, getPorderDetail } from "@api/public";
import Empty from "@components/Empty";
import Loadings from "@components/Loadings";
import localStorage from "@utils/store/localStorage";
import {
  Icon,
  Field,
  Switch,
  Cell,
  Button,
  CellGroup,
  Collapse,
  CollapseItem,
} from "vant";
export default {
  name: "MyMessage",
  components: {
    Empty,
    Loadings,
    [Button.name]: Button,
    [Collapse.name]: Collapse,
    [CollapseItem.name]: CollapseItem,
    [CellGroup.name]: CellGroup,
    [Icon.name]: Icon,
    [Switch.name]: Switch,
    [Field.name]: Field,
    [Cell.name]: Cell,
  },
  data: function () {
    return {
      data: null,
      activeNames: [],
      role: 0,
    };
  },
  created() {
    getPorderDetail({ id: this.$route.params.type }).then((res) => {
      this.data = res.data;
    });

    var jsonData = JSON.parse(localStorage.get("userInfo"));
    this.role = jsonData["role"];
  },
  mounted: function () {},
  methods: {
    subPro: function (index) {
      var data = this.data.detail[index];
      setProduction({
        pcid: data.pcid,
        poid: this.data.data.oid,
        com: data.com_num,
        sum: data.sum_num,
        cstatus: data.cstatus && data.cstatus != 0 ? 1 : 0,
        remark: data.remark,
      }).then((res) => {
        this.$dialog.message("修改成功");
      });
    },
  },
};
</script>
<style scoped>
.container {
  box-shadow: 0 0 5px #eee;
  background: #fff;
  border-radius: 5px;
}
.caption {
  line-height: 50px;
  text-align: center;
}
.item {
  text-align: center;
  font-size: 14px;
  padding: 10px;
  text-align: left;
  color: #666;
  line-height: 22px;
  border-bottom: 1px solid #eee;
}
.item .info div {
  width: 100%;
}
</style>
